import axios from 'axios';

const Index = (props) => (
    <div>
    Yo!
    {props.data}
    </div>
);

Index.getInitialProps = async () => {
    const urlRequest = 'https://jsonplaceholder.typicode.com/todos/';
    const res = await axios.get(urlRequest);
    const {data} = await res;
    console.log(data);
    return {data};
}

export default Index;